.blog_search{
    display: inline-flex;
    align-items: center;
    background-color: rgb(255, 255, 255);
    width: 20%;
    padding: 0 .5rem;
    border-radius: 20px;
    position: sticky;
    left: 0;
    top: 5rem;
}




.blog_body{
    /* border: 1px solid red; */
    /* padding-top: 3rem; */
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
}

.blog_container{
    display: flex;
    width: 50%;
    flex-direction: column;
    margin-bottom: 2rem;
}

.blog_header{
    background-color: white;
    border-radius: 5px;
    padding: .4rem 1rem;
    width: 100%;
    display: flex;
    align-items: center;
}

.blog_logo{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: rgba(197, 197, 197, 0.76);
    padding: 1rem;
}

.blog_info{
    width: 100%;
    height: 300px;
    margin-top: .5rem;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 5px;
}

.blog_footer{
    padding: 1.5rem;
    border-top: 1.5px solid #DFDFDF;
}

.create_post{
    position: fixed;
    bottom: 4rem;
    display: inline;
    justify-content: flex-end;
    right: 1.5rem;
}

.css-1160xiw-MuiPaper-root-MuiDrawer-paper{
    width: 70%; 
    border-radius: 5px 0 0 5px;
    background-color: #f0f2f5 !important;
    box-shadow: none !important;
    
}

.Blog_drawer{
    overflow-x: auto;
}

.heading{
    margin: 0;
}

.blog_header2{
    position: sticky;
    top: 0;
    background-color: #f0f2f5;
    padding: 1rem;
    z-index: 10000;
}

.create_blog{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    min-height: 100vh;

}

.create_blog_body{
    /* min-height: 100vh; */
    /* min-height: 100%; */
    height: 100vh;
    overflow-y: scroll;
}


.blog_footer_action{
    /* position: absolute;
    bottom: 0; */
    /* position: fixed; */
    position: sticky;
    padding: 1rem;
    bottom: 0;
    background-color: #f0f2f5;
    width: fit-content;
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

.back{
    display: flex !important;
    align-items: center !important;
}

.blog_form{
    padding: 0 1rem;
}

.css-1ri6ub7-MuiPaper-root-MuiCard-root{
    min-width: 50% !important;
    margin: 1rem;
    /* box-shadow: none !important; */
}


.search_blog{
    border-radius: 20px;
    position: sticky;
    top: 0;
    left: 0;
    top: 5rem;
    display: inline-flex;
    width: fit-content;
    z-index: 100;
}


.search_bar{
    background-color: transparent;
    border: none;
    outline: none;
    width: 100%;
    display: none;
    margin-right: .5rem;
    transition: all .5s ease;
    /* padding: .5rem 1rem; */
}

.file_selector{
    display: flex;
    flex-direction: column;
}
.file_selector_button{
    display: flex;
    margin-top: 1rem;
}


@media screen and (max-width : 900px) {
    .blog_search{
        width: max-content;
    }

    .blog_container{
        width: 100%;
    }

    .css-1ri6ub7-MuiPaper-root-MuiCard-root{
        min-width: 100% !important;
    }

    .css-1160xiw-MuiPaper-root-MuiDrawer-paper{
        width: 100% !important;
        border-radius: 0px;
    }

}